
import { defineComponent } from 'vue';
import QuestaoEdicao from './QuestaoEdicao.vue';

export default defineComponent({
    name: 'AdicionarQuestao',
    components: {
        QuestaoEdicao,
    },
    props: {
        indice: {
            type: Number,
            required: true,
        },
    },
    data() {
        return {
            emEdicao: false,
        };
    },
    methods: {
        editar() {
            this.emEdicao = true;
        },
        salvou() {
            this.emEdicao = false;
        },
        cancelou() {
            this.emEdicao = false;
        },
    },
    emits: ['salvou'],
});
